        <van-swipe :autoplay="3000" class="vanswipe-banner" indicator-color="white">
          <van-swipe-item
          v-for="(item,index) in nearbyInfo.imageUrls"
          :key="index">
            <img alt :src="item | imageToHttps"/>
          </van-swipe-item>
        </van-swipe>
      </div>
      <!-- 体验店 -->
      <div class="nearshop-containers">
        <ul>
          <li>
            <span class="nearshop-name">{{nearbyInfo.name}}</span>
            <!-- <img alt class="national-img" src="@/assets/imgs/nearshop/national.png" /> -->
            <!-- <span class="national-license">营业执照</span> -->
            <img alt class="sign-img" src="data:image/png;base64,"  @click="isIntroduction" />
            <van-overlay :show="introductionShow" @click="introductionShow = false">
              <div class="wrapper">
                <div class="block">
                  <p class="near-interoduction-title">店铺介绍</p>
                  <div class="interduction-conent">{{introductionText}}</div>
