            距离<em>{{nearbyInfo.distance && nearbyInfo.distance.split('公里')[0]}}</em>公里
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  props: ['nearbyInfo'],
  data() {
    return {
      nearStoreId: 0,
      isToast: true,
      distance: '',
      introductionShow: false,
    }
  },
  methods: {
    isIntroduction() {
      if (this.introductionText === '') {
        Toast('暂无店铺介绍');
        this.introductionShow = false
      } else {
        this.introductionShow = true
      }
    }
  },
  computed: {
    introductionText() {
      let introductionReplace = this.nearbyInfo.introduction.replace(/<\/?.+?>/g, '');
      return introductionReplace.replace(/&nbsp;/ig, '');
    }
  },

}
</script>

