<template>
  <div class="nearshop-bg">
    <div v-if="isFetching"></div>
    <div v-else>
      <!-- 附近的店详细信息 -->
      <nearshopBanner :nearbyInfo="nearbyInfo" />
      <!-- 优惠券 -->
      <nearshopCoupon v-if="false" />
      <!-- 热销商品 -->
      <nearshopHot :nearStoreId="nearStoreId" :storeId="storeId" :hotListdata="hotListdata" :hotListdatas="hotListdatas" />
    </div>
    <!-- bottom 导航 -->
    <div class="bottom-nav">
      <ul>
        <li @click="collectionShop" v-if="hotListdata.storeId">
          <img
            :src="this.iscollected === true ? 'data:image/png;base64,' : 'data:image/png;base64,'"
            height="14"
            width="15"
          />
          <span>{{this.iscollected === true ?'已收藏' : '收藏店铺'}}</span>
        </li>
        <li @click="telBtn">
          <img
            height="15"
            src="data:image/png;base64,"
            width="12"
          />
          <span>联系预约</span>
        </li>
        <li>
          <div @click="goAmap">
            <img
              height="13"
              src="data:image/png;base64,"
              width="13"
            />
            <span>导航到店</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// import AMap from '@/utils/AMap'
// import http from '@/http'
import { Toast } from 'vant'
import utils from '@/utils/utils'
import nearshopBanner from './components/nearshop-banner'
import nearshopCoupon from './components/nearshop-coupon'
import nearshopHot from './components/nearshop-hot'
import { collectStore, nearbyDetail } from '@/http/service/nearshop'
import { commonLoadItemNew, getPriceByProductList } from '@/http/service/shop'
export default {
  components: {
    nearshopBanner,
    nearshopCoupon,
    nearshopHot,
  },
  data() {
    return {
      isFetching: true,
      tel: '',
      collected: true,
      storesData: [],
      iscollected: false, //  是否收藏
      nearStoreId: 0,
      hotListdata: {},
      hotListdatas: [],
      storeId: '',
      nearbyInfo: {},
    }
  },
  methods: {
    nearData(nearData) {
      this.storesData = nearData
      if (nearData.collected) {
        this.iscollected = true
      } else {
        this.iscollected = false
      }
    },
    // 加载商品
    loadProduct() {
      commonLoadItemNew({
        channal: 1, // 渠道，固定值 1
        cityId: this.location.cityId || '173',
        provinceId: this.location.provinceId || '16',
        districtId: this.location.districtId || '2450',
        streetId: this.location.townshipId || '12036596',
        filterData: '',
        fromType: 2,
        keyword: '',
        visitStore: this.hotListdata.storeId, // 访问店铺id
        memberId: sessionStorage.getItem('shareUserId') || '', // 分享人id
        productCateId: '0',
        qs: 'saleDesc',
        pageIndex: 1,
        pageSize: 4,
      }).then(async res => {
        if (res.success) {
          this.finished = res.data.productList.length !== this.pageSize
          if (res.data.productList.length) {
            const _productList = await this.getPriceByProductList(res.data.traceId)
            res.data.productList.forEach(item => {
              _productList.forEach(_item => {
                if (item.sku === _item.sku) {
                  item.finalPrice = _item.lowestFlashPrice || _item.finalPrice
                  item.isLowestFlash = !!_item.lowestFlashPrice
                  item.skku = _item.skku
                  item.commission = _item.commission
                }
              })
            })
            this.hotListdatas = res.data.productList
          } else {
            this.$toast(res.message)
          }
        }
      })
    },
    /**
     * @description 异步获取信息接口(获取商品价钱)
     */
    getPriceByProductList(traceId) {
      return new Promise((resolve, reject) => {
        getPriceByProductList({
          channelFlag: 'mini', // 微商城渠道标识（固定值 mini）
          traceId: traceId,
        }).then(res => {
          if (res.success) {
            resolve(res.products)
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    // 收藏店铺
    collectionShop() {
      // 校验登录状态
      if (this.$store.getters.getLoginStatus) {
        collectStore({
          collectId: this.storesData.storeId,
          // collectId: 63,
          // type: this.storesData.type
          type: 1 //  收藏type 为1
        }).then(res => {
          if (res.data.success) {
            if (res.data.totalCount === 1) {
              this.iscollected = true
              Toast.success('收藏成功')
            } else {
              this.iscollected = false
              Toast.success('取消收藏')
            }
          }
        })
      } else {
        this.$store.dispatch('doLogin').then(res => {
          if (res === 400) {
            console.log('请求异常')
            return
          }
          collectStore({
            collectId: this.storesData.storeId,
            // collectId: 63,
            // type: this.storesData.type
            type: 1 //  收藏type 为1
          }).then(res => {
            if (res.data.success) {
              if (res.data.totalCount === 1) {
                this.iscollected = true
                Toast.success('收藏成功')
              } else {
                this.iscollected = false
                Toast.success('取消收藏')
              }
            }
          })
        })
      }
    },
    // 联系预约客服
    telBtn() {
      window.location.href = 'tel://' + this.storesData.mobile
    },
    // 跳转导航
    goAmap() {
      const data = this.storesData
      if (!data) {
        return ''
      }
      const elat = data.latitude
      const elng = data.longitude

      if (utils.isIos() || utils.isAndroid()) {
        // window.location = this.getAmapURL()
        setTimeout(function() {
          const encode = encodeURI(`${elng},${elat}`)
          const name = data.name// encodeURI(data.name)
          utils.openNewWindow(`https://uri.amap.com/marker?position=${encode}&name=${name}`, true);
          // utils.openNewWindow(`http://api.map.baidu.com/marker?location=${elat},${elng}&title=${data.name}&output=html`, true)
        }, 500);
      } else {
        utils.openNewWindow(this.getAmapURL(), true)
      }
    },
    getAmapURL() {
      const data = this.storesData
      if (!data) {
        return ''
      }
      const elat = data.latitude
      const elng = data.longitude
      if (!utils.isIos() && !utils.isAndroid()) {
        const encode = encodeURI(`${elng},${elat}`)
        const name = encodeURI(data.name)
        return utils.openNewWindow(`https://uri.amap.com/marker?position=${encode}&name=${name}`, true);
        // return `http://api.map.baidu.com/marker?location=${elat},${elng}&title=${data.name}&output=html`
      }
      let proto = utils.isIos() ? 'iosamap://path' : utils.isAndroid ? 'amapuri://route/plan' : ''
      const AppUrl = proto + '?t=0&dlat=' + elat + '&dlon=' + elng + '&dname=' + encodeURI(data.name)
      return AppUrl
    },
  },
  mounted() {
    /**
     * @description 附近的店热销商品
     */
    this.location = { lng: '120.41803', lat: '36.12784' }

    this.$store.dispatch('doLocation', true).then(res => {
      console.log(res, 'doLocation ===')
      if (res !== 400) {
        this.location = {
          lng: res.longitude,
          lat: res.latitude
        }
      }
      // // U+地址
      // if (this.$store.state.locationInfo) {
      //   this.location.lat = this.$store.state.locationInfo.latitude
      //   this.location.lng = this.$store.state.locationInfo.longitude
      // }
      nearbyDetail({
        nearbyId: this.nearStoreId,
        // nearbyId: 63,
        latitude: this.location.lat,
        longitude: this.location.lng
      }).then(res => {
        if (res.data.success) {
          this.storeId = res.data.data.storeId + ''
          this.hotListdata = res.data.data
          this.nearbyInfo = res.data.data
          this.isFetching = false
          this.nearData(res.data.data)
          // console.log(this.hotListdata)
          if (this.hotListdata.storeId) {
            // 存在storeId 时根据店铺经纬度重新切换全局定位 doLocationInfo
            let param = {
              latitude: this.hotListdata.latitude,
              longitude: this.hotListdata.longitude,
            }
            this.$store.dispatch('doLocationInfo', param).then(r => {
              this.location = r
              this.loadProduct()
            })
          }
        }
      })
    })
  },
  created() {
    // 获取附近店参数id
    this.nearStoreId = this.$route.query.nearStoreId
    console.log(this.nearStoreId, '获取店铺id')
  },
}
</script>

