/**
 * 示例文件
 * 可单独封装接口请求方法
 * 文件根据相应功能模块语义化命名
 * 导出至'@/http/index'
 */
import http from '../http'
import url from '../url'
// import Config from '../../config/index'
// 附近的店 热销商品
export const getHotProducts = (params) => {
  return http.request({
    url: url.getHotProducts,
    method: 'get',
    params
  })
}
// 附近的店-详情信息
export const nearbyDetail = (params) => {
  return http.request({
    url: url.nearbyDetail,
    method: 'get',
    params
  })
}
// 关注店铺
export const collectStore = (params) => {
  return http.request({
    url: url.collectStore,
    method: 'get',
    params
  })
}
